import '../css/index.module.scss';
import axios from 'axios';
const json = require('../mock/menu/menu.json');

const hello = (function hello(){

    const menu = document.querySelector('#menu');
    const list = document.querySelector('#list');
    console.log(menu);

    menu.addEventListener('click',function(event){
        var e = event ? event : window.event;
        var target = e .target || e .srcElement;
        // I
        if(target && target.nodeName == 'LI') {
            const menu = target.getAttribute('data-menu');
            showMenu(menu, json[menu]);
        }else{
            
        }
    })

    list.addEventListener('click',function(event){
        var e = event ? event : window.event;
        var target = e .target || e .srcElement;
        // I
        if(target && target.nodeName == 'LI') {
            const src = target.getAttribute('data-src');
            console.log(src);
            showContent(src);
        }else{
            
        }
    })

    // 
    function showMenu(menu, data){

        let li = '';
        data.forEach(element => {
            li += `<li data-src="../mock/markdown/${menu}/${element}.md" >${element}</li>`
        });
        list.innerHTML = `<ul>${li}</ul>`;
    }


    // 
    function showContent(src){
        axios.get(src)
        .then(function (response) {
            // handle success
            console.log(response);
        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
        .then(function () {
            // always executed
        });
    }

})();


export default hello;


